import React from "react";
import Col from "muicss/lib/react/col";

class Tile extends React.Component {
  openLink = (url) => {
    if (window && window.isNativeApp) {
      window.ReactNativeWebView.postMessage(
        JSON.stringify({
          action: "openUrl",
          data: { url },
        })
      );
    } else {
      window.open(url);
    }
  };
  render() {
    return (
      <div className="news-feed">
        <Col xl="4" lg="6" md="6" sm="6" className="news-feed-tile">
          <div
            className="panel mui-panel"
            onClick={() => this.openLink(this.props.value.link)}
          >
            {/* <a href={this.props.value.link} target="_blank" rel="noopener noreferrer"> */}
            <img
              src={this.props.value.image}
              ref="image"
              alt="feed"
              className="feed-image"
            />
            <div className="feed-info">
              <p className="panel-info-value title">{this.props.value.title}</p>
              <p
                className="panel-info-value article-content"
                style={{ color: "gray" }}
              >
                {this.props.value.description}
              </p>
              <p className="panel-info-value desc">
                <span className="article-source">
                  {this.props.value.source}
                </span>{" "}
                |{" "}
                <i>
                  <span className="article-date">{this.props.value.date}</span>
                </i>
              </p>
            </div>
            {/* </a> */}
          </div>
        </Col>
      </div>
    );
  }
}
export default Tile;
