import React from "react";
import { connect } from "react-redux";
import * as newsActions from "../../actions/newsFeedActions";
import { bindActionCreators } from "redux";
import Row from "muicss/lib/react/row";
import Tile from "./Tile";
import Col from "muicss/lib/react/col";
import Divider from "muicss/lib/react/divider";
import Filter from "./Filter";
import * as navigationTitleActions from "../../actions/navigationTitleActions";
import Modal from "react-modal";
import LazyLoad from "react-lazyload";

class newsFeedContainer extends React.Component {
  constructor(props, context) {
    super(props, context);
    this.handleCheckBox = this.handleCheckBox.bind(this);
    this.closeInfoToast = this.closeInfoToast.bind(this);
    this.closeFilterSidebar = this.closeFilterSidebar.bind(this);
    this.openFilterSidebar = this.openFilterSidebar.bind(this);
    this.state = {
      filterSidebar: false,
      filterHeader: false,
    };
  }

  componentDidMount() {
    this.props.navActions.updateNavTitle("News Feed");
    this.props.actions.setupInitialFilter();
    this.props.actions.loadNews();
    if (localStorage.getItem("infoToast")) {
      this.setState({
        filterHeader: false,
      });
    } else {
      this.setState({
        filterHeader: true,
      });
    }
  }

  closeInfoToast() {
    localStorage.setItem("infoToast", false);
    this.setState({
      filterHeader: false,
    });
  }

  openFilterSidebar() {
    this.setState({
      filterSidebar: true,
    });
  }

  closeFilterSidebar() {
    this.setState({
      filterSidebar: false,
    });
  }

  handleCheckBox(id, isShowing) {
    if (id !== 0 && this.props.newsFeed.originalFilter[0].isShowing === true) {
      this.props.actions.updateFilter(0, false);
      this.props.actions.updateFilter(id, isShowing);
    }
    if (id === 0 && isShowing === true) {
      this.props.actions.resetFilter();
      // this.props.actions.loadNews();
    }
    if (id === 0 && isShowing === false) {
      this.props.actions.setAllFilterToNone();
    } else {
      this.props.actions.updateFilter(id, isShowing);
    }
  }

  render() {
    let filter = null;
    var filterCounter = 0;
    var newsCounter = 0;

    filter =
      this.props.newsFeed.originalFilter &&
      this.props.newsFeed.originalFilter.map((item) => (
        <Filter
          key={filterCounter++}
          value={item}
          handleCheckBox={this.handleCheckBox}
        />
      ));

    return (
      <div className="newsFeed">
        <Row>
          <Modal
            className="loading-modal"
            isOpen={this.props.newsFeed.loadingNews}
            ariaHideApp={false}
            contentLabel="Modal"
            shouldCloseOnOverlayClick={false}
          >
            <div
              className={
                this.props.newsFeed.loadingNews ? "centered-spinner" : "hidden"
              }
            >
              <img
                src="/images/icons/spin.svg"
                className="loading-spinner"
                alt="loading spinner"
              />
            </div>
          </Modal>
          <Col lg="12" md="12" sm="12">
            <div className={this.state.filterHeader ? "filter-info" : "hidden"}>
              <p>
                <i
                  className="material-icons close-icon"
                  onClick={this.closeInfoToast}
                >
                  close
                </i>
                <i className="material-icons info-icon">info</i>Tap the filter
                menu icon on the upper right to filter the news feed...
              </p>
            </div>
            {this.props.newsFeed.filteredNews &&
              this.props.newsFeed.filteredNews.map((item) => (
                <LazyLoad height={200} offset={100} once>
                  <Tile key={newsCounter++} id={newsCounter} value={item} />
                </LazyLoad>
              ))}
            ;
          </Col>
          <div
            className="filters-open-container mui--z1"
            onClick={this.openFilterSidebar}
          >
            <i className="material-icons">filter_list</i>
            <p>Filter</p>
          </div>
          <div
            className={
              this.state.filterSidebar
                ? "filters-close-container mui--z1"
                : "hidden"
            }
            onClick={this.closeFilterSidebar}
          >
            <i className="material-icons">arrow_forward</i>
          </div>
          <div
            className={
              this.state.filterSidebar ? "filter-container mui--z1" : "hidden"
            }
          >
            <div className="filters-header">
              <i className="material-icons">filter_list</i>
              <p>Filter</p>
            </div>
            <Divider className="filter-divider" />
            <div className="filters">{filter}</div>
          </div>
        </Row>
      </div>
    );
  }
}

function mapStateToProps(state, ownProps) {
  return {
    newsFeed: state.newsFeed,
    title: state.navTitle,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(newsActions, dispatch),
    navActions: bindActionCreators(navigationTitleActions, dispatch),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(newsFeedContainer);
