import * as types from "./actionTypes";
import { ajax } from "../lib";
import moment from "moment";

export function loadNews() {
  return (dispatch) => {
    dispatch({ type: types.LOADING_NEWS });
    ajax({
      url: "https://easternunionblog.com/category/featured1/feed/",
      crossDomain: true,
      parseXML: true,
      success: async function (result) {
        let arrayNews = [];
        let item = result.getElementsByTagName("item");
        var numItems = item.length;
        var counter = 0;
        for (let x = 0; x < numItems; x++) {
          let item = result.getElementsByTagName("item")[x];
          let title = item.getElementsByTagName("title");
          let newTitle = title[0].textContent
            .replace(/&amp;/g, "&")
            .replace(/amp;/g, "&");
          try {
            newTitle = decodeURIComponent(newTitle);
          } catch (err) {}
          let link = item.getElementsByTagName("link");
          let time = item.getElementsByTagName("pubDate")[0];
          let pubDate = moment(new Date(time.innerHTML)).format(
            "ddd, MMM Do, YYYY, h:mm A"
          );

          //build list of categories in item
          let originalCategory = item.getElementsByTagName("category");
          let categoryList = [];
          for (let y = 1; y < originalCategory.length; y++) {
            var category = originalCategory[y].textContent;
            categoryList.push(category);
          }

          let enclosure = item.getElementsByTagName("enclosure");
          let image = "/images/default-thumbnail.jpg";
          if (enclosure.length) {
            image = enclosure[0].attributes["url"].value;
          }
          let creator = item.getElementsByTagNameNS(
            "http://purl.org/dc/elements/1.1/",
            "creator"
          )[0];
          let source = creator.textContent;
          if (source.length > 18) {
            source = source.substring(0, 18) + "..";
          }

          //description get info from 1st innerDiv and format
          let description = item.getElementsByTagName("description")[0];
          let text = description.textContent;
          let newDiv = document.createElement("div");
          newDiv.innerHTML = text;
          let innerDivs = newDiv.getElementsByTagName("div");
          let finalText =
            innerDivs.length === 1
              ? innerDivs[0].innerText
              : innerDivs[1].innerText;
          let elem = document.createElement("div");
          elem.innerHTML = text;
          let newDescription = finalText.replace(/&amp;/g, "&");

          let Item = {
            id: counter++,
            title: newTitle.substring(0, 90) + "..",
            link: link[0].innerHTML,
            date: pubDate,
            description: newDescription.substring(0, 100) + "..",
            source: source,
            image: image,
            category: categoryList,
          };

          arrayNews.push(Item);
        }
        dispatch({ type: types.GET_NEWS, news: arrayNews });
      },
    });
  };
}

export function setupInitialFilter() {
  return (dispatch) => {
    ajax({
      url: "https://easternunionblog.com/category/featured1/feed/",
      crossDomain: true,
      parseXML: true,
      success: function (result) {
        let arrayCategories = [];
        let item = result.getElementsByTagName("item");
        var numItems = item.length;
        for (let x = 0; x < numItems; x++) {
          let item = result.getElementsByTagName("item")[x];
          let originalCategory = item.getElementsByTagName("category");
          var category =
            originalCategory[1] && originalCategory[1].textContent
              ? originalCategory[1].textContent
              : "";
          if (originalCategory[2]) {
            var category2 = originalCategory[2].textContent;
            arrayCategories.push(category2);
          }
          if (originalCategory[3]) {
            var category3 = originalCategory[3].textContent;
            arrayCategories.push(category3);
          }
          arrayCategories.push(category);
        }
        var uniques = arrayCategories.filter(function (item, i, ar) {
          return ar.indexOf(item) === i;
        });
        var uniqueFullCategories = [];
        var counter = 1;
        let allItem = {
          id: 0,
          category: "ALL",
          isShowing: false,
        };
        uniqueFullCategories.push(allItem);
        for (let x = 0; x < uniques.length; x++) {
          if (uniques[x] === "NY/NJ" || uniques[x] === "DC/MD") {
            let Item = {
              id: counter++,
              category: uniques[x],
              isShowing: true,
            };
            uniqueFullCategories.push(Item);
          } else {
            let Item = {
              id: counter++,
              category: uniques[x],
              isShowing: false,
            };
            uniqueFullCategories.push(Item);
          }
        }
        dispatch({
          type: types.GET_INITIAL_NEWS_FILTER,
          filter: uniqueFullCategories,
        });
      },
    });
  };
}

