import React from "react";
import Col from "muicss/lib/react/col";

export default class Filter extends React.Component {
  render() {
    return (
      <div>
        <Col md="12">
          <div className="rkmd-checkbox checkbox-ripple">
            <label className="input-checkbox checkbox-lightBlue">
              <input
                type="checkbox"
                id="checkbox-1"
                checked={this.props.value.isShowing}
                onChange={() => {
                  this.props.handleCheckBox(
                    this.props.value.id,
                    !this.props.value.isShowing
                  );
                }}
              />
              <span className="checkbox"></span>
              <span className="ripple"></span>
            </label>
            <label htmlFor="checkbox-1" className="label">
              {this.props.value.category}
            </label>
          </div>
        </Col>
      </div>
    );
  }
}
